$mobileBreakpoint: 500px;
$tabletBreakpoint: 768px;
$desktopBreakpoint: 1100px;
$largeDesktopBreakpoint: 1366px;
$extraLargeDesktopBreakpoint: 1920px;

@mixin media-less-than-tablet {
    @media (max-width: $tabletBreakpoint - 1) {
        @content;
    }
}

@mixin media-less-than-desktop {
    @media (max-width: $desktopBreakpoint - 1) {
        @content;
    }
}

@mixin media-mobile {
    @media (min-width: $mobileBreakpoint) {
        @content;
    }
}

@mixin media-tablet {
    @media (min-width: $tabletBreakpoint) {
        @content;
    }
}

@mixin media-desktop {
    @media (min-width: $desktopBreakpoint) {
        @content;
    }
}

@mixin media-large-desktop {
    @media (min-width: $largeDesktopBreakpoint) {
        @content;
    }
}

@mixin media-extra-large-desktop {
    @media (min-width: $extraLargeDesktopBreakpoint) {
        @content;
    }
}
